import React from 'react';
import staticSound from './mp3/static.mp3';
import music from './mp3/music.mp3';
import podcast from './mp3/podcast.mp3';
import documentary from './mp3/documentary.mp3';
import './App.css';

class App extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {percentage: 1};
    this.setPercentage = this.setPercentage.bind(this)
  }
  
  render() {
    return (
      <div className="App">
        <h1>Fake Fake News</h1>
        <AudioPopup></AudioPopup>
        <Bar percentage={this.state.percentage} />
        <Dial changeState={this.setPercentage} />
      </div>
    );
  }

  setPercentage(newPercentage) {
    this.setState({
      percentage: newPercentage
    });
  }
  
}

class Dial extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {angle: 2.7};
  }
  
  render() {
    return(
      <div id="radio-dial" style={{transform: `rotate(${this.state.angle - 45}deg)`}}>
        <div id="radio-dial-tab"></div>
      </div>
    );
  }

  componentDidMount() {
    document.getElementById('popup').addEventListener('click', () => {
      window.addEventListener('wheel', (e) => {
        let newAngle = this.state.angle + e.deltaY / Math.abs(e.deltaY) * 2.7;
        if (newAngle > 0 && newAngle < 270) {
          this.setState({angle: newAngle});
  
          let newPercentage = Math.trunc(newAngle / 270 * 100);
          this.props.changeState(newPercentage);
        }
      });
    });
  }

}

class Bar extends React.Component {
  
  render() {
    let newLeft = this.props.percentage + '%';
    return(
      <div id="radio-bar">
        <div id="radio-bar-tab" style={{left: newLeft}}></div>
        <audio loop id="music" src={music}></audio>
          <audio loop id="staticsound" src={staticSound}></audio>
          <audio loop id="podcast" src={podcast}></audio>
          <audio loop id="documentary" src={documentary}></audio>
        <div className="radio-bar-stop" id="radio-bar-stop-1"></div>
        <div className="radio-bar-stop" id="radio-bar-stop-2"></div>
        <div className="radio-bar-stop" id="radio-bar-stop-3"></div>
      </div>
    )
  }

  componentDidMount() {
    document.getElementById('popup').addEventListener('click', () => {
      this.initializeAudio();
    });
    this.lastVolume = 1;
  }

  initializeAudio() {
    document.getElementById('music').volume = .1;
    document.getElementById('documentary').currentTime = 30;
    document.getElementById('staticsound').currentTime = 10;
    document.getElementById('staticsound').volume = 1;
    document.getElementById('staticsound').play();
    document.getElementById('podcast').addEventListener('volumechange', (e) => {
      let volume = document.getElementById('podcast').volume;
      if ((volume === this.lastVolume) && (this.lastVolume !== 0)) {
        volume = 0;
      }
      console.log(volume)
      document.getElementById('music').volume = volume / 10;
      if (this.staticVolume != 0) {
        document.getElementById('staticsound').volume = volume;
      }
      document.getElementById('documentary').volume = volume;
      this.lastVolume = volume;
    });
  }

  componentDidUpdate() {

    if (this.props.percentage > 20 && this.props.percentage < 30) {
      this.staticVolume = Math.abs(25 - this.props.percentage);
      document.getElementById('music').play();
      document.getElementById('podcast').pause();
      document.getElementById('documentary').pause();
      document.getElementById('podcast').removeAttribute('controls');
    } else if (this.props.percentage > 45 && this.props.percentage < 55) {
      this.staticVolume = Math.abs(50 - this.props.percentage);
      document.getElementById('music').pause();
      document.getElementById('podcast').play();
      document.getElementById('documentary').pause();
      document.getElementById('podcast').setAttribute('controls', 'true');
    } else if (this.props.percentage > 70 && this.props.percentage < 80) {
      this.staticVolume = Math.abs(75 - this.props.percentage);
      document.getElementById('music').pause();
      document.getElementById('podcast').pause();
      document.getElementById('documentary').play();
      document.getElementById('podcast').removeAttribute('controls');
    } else {
      document.getElementById('music').pause();
      document.getElementById('podcast').pause();
      document.getElementById('documentary').pause();
      document.getElementById('podcast').removeAttribute('controls');
    }

    if (this.staticVolume !== 0 && this.staticVolume < 5) {
      document.getElementById('staticsound').volume = document.getElementById('podcast').volume - (document.getElementById('podcast').volume / this.staticVolume);
    } else if (this.staticVolume === 0) {
      document.getElementById('staticsound').volume = 0;
    } else {
      document.getElementById('staticsound').volume = document.getElementById('podcast').volume;
    }

  }

}

class AudioPopup extends React.Component {

  render() {
    return (
      <div id="popup">
        <div id="popup-background"></div>
        <div id="popup-message">
          <p>
          Questo sito è una ricostruzione digitale di una radio analogica.<br/>
          Ogni barretta nella icona delle frequenze contiene un canale diverso. Ogni canale inizia non appena selezionato.<br/>
          Portare la lancetta sul primo per il canale musicale.<br/>
          Portare la lancetta sul secondo per il canale contenente il podcast.<br/>
          Portare la lancetta sul terzo per il canale delle notizie.<br/>
          Per spostare la si può utilizzare la rotellina del mouse oppure il touchpad con entrambe le dita.<br/>
          Sotto la barra delle frequenze troverete i comandi per il podcast, sarà possibile riavvolgere ed aumentare/diminuire il volume.<br/>
          </p>
          <p>
          Fare click su questa finestra la chiuderà e darà accesso al sito,<br/>
          Buon ascolto.<br/>
          </p>
        </div>
      </div>
    );
  }

  componentDidMount() {
    document.getElementById('popup').addEventListener('click', () => {
      document.getElementById('popup').outerHTML = '';
    });
  }

}

export default App;
